import { createSelector } from 'reselect';

const cardsSelector = state => state.cards;
const scirtsSelector = state => state.scirts;

export const getCards = createSelector(cardsSelector, cards => cards);

export const getScirt = createSelector(
    scirtsSelector,
    scirts => scirts.find(scirt => scirt.isSelected).image
);



