import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { selectScirt } from './../../actions';

import Scirt from './../presentational/Scirt';

export class ScirtsContainer extends Component {
    handleClick = id => {
        this.props.selectScirt(id);
    };

    render() {
        const { scirts } = this.props;

        return scirts.map(scirt => (
            <Scirt
                key={scirt.id}
                isSelected={scirt.isSelected}
                image={scirt.image}
                handleClick={() => this.handleClick(scirt.id)}
            />
        ));
    }
}

ScirtsContainer.propTypes = {
    scirts: PropTypes.array.isRequired,
    selectScirt: PropTypes.func.isRequired,
};

export default connect(
    state => ({
        scirts: state.scirts,
    }),
    { selectScirt }
)(ScirtsContainer);



